body {
  margin: 0;
  padding: 0;
  font-family: "Segoe UI", "Roboto", "Oxygen", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: 'Segoe UI', "Roboto", sans-serif !important; }

.cv-main-layout {
  padding: 0px; }
  .cv-main-layout .cv-header-wrapper {
    border: 1px;
    background-color: green;
    height: 200px;
    background-image: url(/static/media/image4.7db25367.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 20%;
    background-attachment: fixed; }
  .cv-main-layout .cv-sidebar-wrapper {
    border: 1px;
    background-color: #ffd079;
    height: 100%; }
  .cv-main-layout .cv-content-wrapper {
    border: 1px;
    background-color: #e4e3e3;
    height: 100%;
    padding: 20px; }

.ui.grid > .row {
  padding: 0px 0px !important; }
  .ui.grid > .row .column {
    padding: 0px !important; }

.cv-main-sidebar {
  padding: 10px;
  padding: 10px 0px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.8); }
  .cv-main-sidebar .cv-header-sidebar-wrapper {
    text-align: center; }

.sb-content-personal-title__wrapper {
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  margin-bottom: 10px; }
  .sb-content-personal-title__wrapper .sb-content-personal__title {
    float: left;
    text-transform: uppercase;
    padding: 10px 0; }

.cv-sidebar-content__personal {
  display: flex;
  flex-direction: row; }
  .cv-sidebar-content__personal .sb-personal__title {
    font-weight: bold;
    font-weight: 0.25rem;
    line-height: 1.5rem; }
  .cv-sidebar-content__personal .sb-personal__description {
    line-height: 1.5rem; }

.cv-content-block {
  margin: 10px 0px; }

.cv-content-profile__sumary .pf-sumary__title {
  text-transform: uppercase;
  border-bottom: 1px solid rgba(0, 0, 0, 0.12);
  font-weight: bold; }

.cv-content-profile__sumary .pf-sumary__block_description {
  text-indent: 20px;
  margin: 10px 0; }
  .cv-content-profile__sumary .pf-sumary__block_description .pf-sumary__description {
    line-height: 1.5rem; }

